.directory {
  background-color: #fef9f5;
}

.directory ul {
  margin: 0;
  padding: 0;
}

.directory ul li {
  margin-bottom: 10px;
  list-style: none;
}

.directory a {
  font-size: 16px;
  font-weight: 400;
  color: #283106;
  text-decoration: none;
}

.directory a:hover {
  font-weight: 600;
}

.directory a.active {
  color: #777e5c;
  font-weight: 500;
  position: relative;
}

.directory a.active::after {
  content: "";
  position: absolute;
  bottom: -4px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #777e5c;
}

